import { Fragment } from 'react';
import classNames from 'classnames';
import { Transition } from 'react-transition-group';
import Link from 'components/Link';
import DecoderText from 'components/DecoderText';
import Section from 'components/Section';
import { reflow } from 'utils/transition';
import Heading from 'components/Heading';
import Text from 'components/Text';
import './Profile.css';
import qrcode from 'assets/profile/qrcode.png';

const DownLoads = ({ status, titleId }) => (
  <Fragment>
    <div class="downloads">
      <div class="qrcode">
        <div class="text">QRCODE DOWNOLOAD</div>
        <div class="qrcode_wrapper">
          <img src={qrcode}></img>
        </div>
      </div>
      <div class="division_bar"></div>
      <div class="appstore">
        <div class="text">APPSTORE DOWNLOAD</div>
        <a href="https://play.google.com/store/apps/details?id=com.killerwhale.Diveflash">
          <button class="button_appstore">
            <i class="fab fa-google-play"></i>&nbsp;&nbsp;Google play
          </button>
        </a>
        <a href="#">
          <button class="button_appstore">
            <i class="fab fa-app-store-ios"></i>&nbsp;&nbsp;App Store
          </button>
        </a>
      </div>
    </div>
  </Fragment>
);

const ProfileText = ({ status, titleId }) => (
  <Fragment>
    <Heading
      className={classNames('profile__title', `profile__title--${status}`)}
      level={3}
      id={titleId}
    >
      <DecoderText text="We are DiveFlash" start={status !== 'exited'} delay={500} />
    </Heading>
    <Text
      className={classNames('profile__description', `profile__description--${status}`)}
      size="l"
    >
      A whole new diving experience is in your hand.
      <br /> Enhance your underwater pictures that are taken before.
      <br /> You are only one tap away from processing your underwater pictures.
      <br /> We process your underwater pictures automatically through our own underwater
      image enhancement algorithms.
    </Text>
    <Text
      className={classNames('profile__description', `profile__description--${status}`)}
      size="l"
    >
      {' '}
      We provide underwater image enhancement filters for free. <br />
      Our each filter has it's own theme.
      <br /> You can turn your unprocessed pictures into one of your best shots.
      <br /> Make the best of your underwater images and cherish the moments you had
      underwater.
    </Text>
  </Fragment>
);

const Profile = ({ id, visible, sectionRef }) => {
  const titleId = `${id}-title`;

  return (
    <Section
      className="profile"
      as="section"
      id={id}
      ref={sectionRef}
      aria-labelledby={titleId}
      tabIndex={-1}
    >
      <Transition in={visible} timeout={0} onEnter={reflow}>
        {status => (
          <div className="profile__content">
            <div className="profile__column">
              <ProfileText status={status} titleId={titleId} />
              <DownLoads></DownLoads>
            </div>
          </div>
        )}
      </Transition>
    </Section>
  );
};

export default Profile;
