const sendMail = () => {
  const mailto = 'mailto:mail@gmail.com?subject=Test subject&body=Body content';
  window.location = mailto;
  window.location.href = mailto;
};

const ContactTitle = ({ status, titleId }) => (
  <Fragment>
    <Heading
      level={3}
      className={classNames('contact__title', `contact__title--${status}`)}
    >
      <DecoderText text="Contact Us" start={status !== 'exited'} delay={500} />
    </Heading>
  </Fragment>
);
const ContactText = ({ status, titleId }) => {
  //modal
  const [modalOpen, setModalOpen] = useState(false);

  const openModal = () => {
    setModalOpen(true);
  };
  const closeModal = () => {
    setModalOpen(false);
  };

  return (
    <Fragment>
      <Text className="title">Diveflash</Text>
      <Text size="l" weight="medium">
        <br />
        diveflashx@gmail.com
      </Text>
      <br />
      <div className={classNames('contact__button', `contact__button--${status}`)}>
        <Button iconHoverShift onClick={sendMail} iconEnd="arrowRight">
          contact us
        </Button>
      </div>
      {/* <div className={classNames('contact__button', `contact__button--${status}`)}>
        <Button iconHoverShift href="/email" iconEnd="arrowRight">
          contact us
        </Button>
      </div> */}
    </Fragment>
  );
};

